import React, { useContext, useEffect, useRef, useState } from 'react'
import FormInline from 'components/urgudul_components/formInline'
import HelpPopup from 'components/help_popup/helpPopup'
import ButtonTooltip from 'components/button_tooltip/buttonTooltip'
import PlusCircleSVG from 'assets/svgComponents/plusCircleSVG'
import MinusCircleSVG from 'assets/svgComponents/minusCircleSVG'
import FormOptions from 'components/urgudul_components/formOptions'
import PenSVG from 'assets/svgComponents/penSVG'
import FormRichText from 'components/urgudul_components/formRichText'
import axios from 'axiosbase'
import UrgudulContext from 'components/utilities/urgudulContext'
import AlertContext from 'components/utilities/alertContext'
import { useHistory } from 'react-router-dom'
import getLoggedUserToken from 'components/utilities/getLoggedUserToken'
import UploadSVG from 'assets/svgComponents/uploadSVG'
import FileCard from 'pages/attachments/fileCard'
import PaperClipSVG from 'assets/svgComponents/paperClipSVG'
import FilePreviewContext from 'components/utilities/filePreviewContext'
import TreeSelect from 'components/urgudul_components/treeSelect'
import LoadFromOtherProject from '../loadFromOtherProject'


const initialState = [
    {
        company_name: null,
        representative_name: null,
        representative_phone: null,
        representative_email: null,
        business_sectorId: null,
        company_size: null,
        support_recipient: null,
        project_contribution: null,
        attachedFiles: null,
    },
]

function UrgudulClusters({ projects }) {
    const [form, setForm] = useState(initialState)

    const UrgudulCtx = useContext(UrgudulContext)

    useEffect(() => {
        if (UrgudulCtx.data.clusters && UrgudulCtx.data.clusters?.length) {
            setForm(UrgudulCtx.data.clusters)
        }
    }, [UrgudulCtx.data.id])

    const handleInput = (e) => {
        const newForm = form
        newForm[e.target.id][e.target.name] = e.target.value
        setForm([...newForm])
    }

    const handleInputFormatted = (values, key, index) => {
        const newForm = form
        newForm[index][key] = values.formattedValue
        setForm([...newForm])
    }

    const [editIndex, setEditIndex] = useState()

    const fileInputRef = useRef()

    const handleFileInputClick = (index) => {
        setEditIndex(index)
        fileInputRef.current.value = null
        fileInputRef.current.click()
    }

    const handleInputFile = (e) => {
        const formData = new FormData()
        if (!e.target.files[0]) return
        formData.append('file', e.target.files[0])
        formData.append('description', '     ')
        const newForm = form
        newForm[editIndex].attachedFiles = 'loading'
        setForm([...newForm])

        axios.post('attach-files', formData, {
            headers: {
                'Authorization': getLoggedUserToken(),
                'Content-Type': 'multipart/form-data',
            }
        }).then(res => {
            const newForm = form
            newForm[editIndex].attachedFiles = [res.data.data]
            setForm([...newForm])
        }).catch(err => {
            const newForm = form
            newForm[editIndex].attachedFiles = null
            setForm([...newForm])
            AlertCtx.setAlert({ open: true, variant: 'error', msg: ' .    .' })
        })
    }

    const handleRemoveFile = (index) => {
        const newForm = form
        newForm[index].attachedFiles = null
        setForm([...newForm])
    }

    const FilePreviewCtx = useContext(FilePreviewContext)

    const handleDownloadFile = (index) => {
        axios.get(`attach-files/${form[index].attachedFiles[0].id}`, {
            headers: {
                'Authorization': getLoggedUserToken(),
            },
            responseType: 'blob',
        }).then(res => {
            const URL = window.URL.createObjectURL(res.data)
            FilePreviewCtx.setFile({ open: true, src: URL })
        }).catch(err => {
            AlertCtx.setAlert({ open: true, variant: 'error', msg: ' .   .' })
        })
    }

    const handleAdd = () => {
        const newObj = {
            company_name: null,
            representative_name: null,
            representative_phone: null,
            representative_email: null,
            business_sectorId: null,
            company_size: null,
            support_recipient: null,
            project_contribution: null,
            attachedFiles: null,
        }

        setForm([...form, newObj])
    }

    const handleRemove = (index) => {
        setForm(form.filter((_, i) => i !== index))
    }

    const handleSetForm = (key, value, index) => {
        setForm(prev => {
            const newForm = [...prev]
            newForm[index][key] = value
            return newForm
        })
    }

    const AlertCtx = useContext(AlertContext)

    const history = useHistory()

    const handleSubmit = () => {
        setValidate(true)
        let allValid = true
        for (const obj of form) {
            allValid = allValid && Object.keys(initialState[0]).every(key => !checkInvalid(obj[key], key === 'project_contribution' && 'quill'))
        }

        if (form.length < 1) {
            AlertCtx.setAlert({ open: true, variant: 'normal', msg: '  1     .' })
            return
        }

        if (UrgudulCtx.data.id) {
            if (allValid) {
                axios.put(`projects/${UrgudulCtx.data.id}`, { clusters: form }, {
                    headers: {
                        'Authorization': getLoggedUserToken()
                    }
                }).then(res => {
                    UrgudulCtx.setData({ ...UrgudulCtx.data, ...res.data.data })
                    AlertCtx.setAlert({ open: true, variant: 'success', msg: '  .' })
                    history.push('/urgudul/4')
                }).catch(err => {
                    AlertCtx.setAlert({ open: true, variant: 'error', msg: ' ,  .' })
                })
            } else {
                AlertCtx.setAlert({ open: true, variant: 'normal', msg: '    .    .' })
            }
        } else {
            AlertCtx.setAlert({ open: true, variant: 'normal', msg: '   .     ,  .' })
            history.push('/urgudul/1')
        }
    }

    const [validate, setValidate] = useState(false)

    const checkInvalid = (value, type) => {
        switch (value) {
            case null:
                return true
            case '':
                return true
            case '<p><br></p>':
                if (type === 'quill') return true
                else return false
            default:
                return false
        }
    }

    const [sectors, setSectors] = useState([])

    useEffect(() => {
        axios.get('business-sector').then(res => {
            setSectors(res.data.data)
        })
    }, [])

    const otherProjects = projects.filter(project => project.id !== UrgudulCtx.data.id)

    const loadFromOtherProjectCluster = (id) => {
        axios.get(`projects/${id}`, {
            headers: { Authorization: getLoggedUserToken() },
        }).then(res => {
            const loadCluster = res.data.data?.clusters ?? []
            if (loadCluster.length > 0) {
                setForm(loadCluster)
            } else {
                AlertCtx.setAlert({ open: true, variant: 'normal', msg: '     .' })
                return
            }
            AlertCtx.setAlert({ open: true, variant: 'success', msg: '    .' })
        }).catch(err => {
            AlertCtx.setAlert({ open: true, variant: 'error', msg: ' .     .' })
        })
    }

    return (
        <div className="tw-relative tw-mt-8 tw-py-2 tw-rounded-lg tw-shadow-md tw-min-w-min tw-w-11/12 tw-max-w-5xl tw-mx-auto tw-border-t tw-border-gray-100 tw-bg-white tw-divide-y tw-divide-dashed">
            <div className="">
                <div className="tw-font-medium tw-p-3 tw-flex tw-items-center tw-text-15px tw-relative">
                    <span className="tw-text-blue-500 tw-text-xl tw-mx-2">A2</span>
                    <span className="tw-leading-tight">-   </span>

                    <HelpPopup classAppend="tw-ml-4 tw-mr-2 sm:tw-ml-12" main="  ,     ,    ,   ." position="bottom" />

                    <LoadFromOtherProject classAppend="tw-absolute tw-right-4" otherProjects={otherProjects} loadFromOtherProject={loadFromOtherProjectCluster} />
                </div>

                {UrgudulCtx.data.project_number &&
                    <div className="tw-ml-5 tw-mb-2 tw-font-medium tw-text-13px">
                         :
                        <span className="tw-text-blue-500 tw-ml-2">{UrgudulCtx.data.project_number}</span>
                    </div>
                }
            </div>

            <input className="tw-absolute tw-invisible" type="file" onChange={handleInputFile} ref={fileInputRef} />

            {form.map((item, i) =>
                <div className="tw-flex even:tw-bg-gray-50" key={i}>
                    <div className="tw-flex-grow">
                        <div className="tw-flex-grow tw-grid tw-grid-cols-1 md:tw-grid-cols-2 tw-place-items-start">
                            <FormInline label="    " type="text" value={item.company_name || ''} name="company_name" id={i} onChange={handleInput} classAppend="tw-w-full tw-max-w-lg" classLabel={i % 2 === 1 && 'tw-bg-gray-50'} classInput="tw-w-full" invalid={validate && checkInvalid(item.company_name)} />

                            <FormInline label="   " type="text" value={item.representative_name || ''} name="representative_name" id={i} onChange={handleInput} classAppend="tw-w-full tw-max-w-lg" classLabel={i % 2 === 1 && 'tw-bg-gray-50'} classInput="tw-w-full" invalid={validate && checkInvalid(item.representative_name)} />

                            <FormInline label=" " type="numberFormat" formats={{ format: '(+976) #### ####' }} value={item.representative_phone || ''} name="representative_phone" id={i} onChange={handleInputFormatted} classAppend="tw-w-full tw-max-w-lg" classLabel={i % 2 === 1 && 'tw-bg-gray-50'} classInput="tw-w-40" invalid={validate && checkInvalid(item.representative_phone)} />

                            <FormInline label=" " type="email" value={item.representative_email || ''} name="representative_email" id={i} onChange={handleInput} classAppend="tw-w-full tw-max-w-lg" classLabel={i % 2 === 1 && 'tw-bg-gray-50'} classInput="tw-w-full" validate={true} invalid={validate && checkInvalid(item.representative_email)} />

                            <TreeSelect data={sectors} label="" displayName="bdescription_mon" value={item.business_sectorId} name="business_sectorId" index={i} handleChange={handleSetForm} invalid={validate && checkInvalid(item.business_sectorId)} />

                            <div className="tw-w-full tw-max-w-lg tw-flex">
                                <FormOptions label="   " options={['', '', '']} values={[1, 2, 3]} value={item.company_size} name="company_size" id={i} setForm={handleSetForm} classAppend="tw-flex-grow" classLabel={i % 2 === 1 && 'tw-bg-gray-50'} invalid={validate && checkInvalid(item.company_size)} />

                                <div className="tw-relative tw-w-2 tw-ml-auto">
                                    <HelpPopup classAppend="tw-right-5 tw-top-1" main="               .  :" list={["$30M  30           ."]} position="top-left" />
                                </div>
                            </div>

                            <div className="tw-p-2 tw-max-w-lg">
                                <table className="tw-text-sm tw-w-full">
                                    <thead>
                                        <tr className="tw-h-8">
                                            <th className="tw-font-medium tw-text-center">   </th>
                                            <th className="tw-font-medium tw-text-center">  (.)</th>
                                            <th className="tw-font-medium tw-text-center">   </th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr className="tw-h-8 tw-bg-blue-100">
                                            <td className="tw-pl-2"></td>
                                            <td className="tw-pl-2">{'< $50 '}</td>
                                            <td className="tw-pl-2">{'< 10'}</td>
                                        </tr>
                                        <tr className="tw-h-8">
                                            <td className="tw-pl-2"></td>
                                            <td className="tw-pl-2">{'> $50   $10 '}</td>
                                            <td className="tw-pl-2">{'>=10, <50'}</td>
                                        </tr>
                                        <tr className="tw-h-8 tw-bg-blue-100">
                                            <td className="tw-pl-2"></td>
                                            <td className="tw-pl-2">{'> $10   $50 '}</td>
                                            <td className="tw-pl-2">{'>=50, <250'}</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>

                            <FormOptions label="    " options={['', '']} values={[true, false]} value={item.support_recipient} name="support_recipient" id={i} setForm={handleSetForm} classAppend="tw-w-full tw-max-w-lg" classLabel={i % 2 === 1 && 'tw-bg-gray-50'} invalid={validate && checkInvalid(item.support_recipient)} />
                        </div>

                        <div className="tw-w-full">
                            <div className="tw-flex tw-items-center tw-p-2 tw-mt-1">
                                <PenSVG className={`tw-w-5 tw-h-5 ${validate && checkInvalid(item.project_contribution, 'quill') ? 'tw-text-red-500' : 'tw-text-gray-600'} tw-transition-colors`} />
                                <span className={`tw-ml-2 tw-text-sm tw-font-medium ${validate && checkInvalid(item.project_contribution, 'quill') && 'tw-text-red-500'} tw-transition-colors`}>
                                     ,    
                                </span>

                                <HelpPopup classAppend="tw-ml-auto" main="  ,   ,  ,     ." position="top-left" />
                            </div>

                            <div className="tw-py-2 tw-px-4 tw-h-40 tw-resize-y tw-overflow-y-hidden" style={{ minHeight: '128px', maxHeight: '768px' }}>
                                <FormRichText modules="small" value={item.project_contribution || ''} name="project_contribution" id={i} setForm={handleSetForm} />
                            </div>
                        </div>

                        <div className="tw-w-full">
                            <div className="tw-flex tw-items-center tw-p-2 tw-mt-1">
                                <UploadSVG className={`tw-w-5 tw-h-5 ${validate && checkInvalid(item.attachedFiles) ? 'tw-text-red-500' : 'tw-text-gray-600'} tw-transition-colors`} />
                                <span className={`tw-ml-2 tw-text-sm tw-font-medium ${validate && checkInvalid(item.attachedFiles) && 'tw-text-red-500'} tw-transition-colors`}>
                                       
                                </span>

                                <HelpPopup classAppend="tw-ml-auto" main="             ." position="top-left" />
                            </div>

                            {item.attachedFiles ?
                                <FileCard name={item.attachedFiles[0]?.name} type={item.attachedFiles[0]?.mimetype} size={item.attachedFiles[0]?.size} classAppend="tw-ml-6 tw-mb-4" uploading={item.attachedFiles === 'loading' && true} removeFile={() => handleRemoveFile(i)} downloadFile={() => handleDownloadFile(i)} />
                                :
                                <button className="tw-ml-6 tw-mb-4 tw-py-1.5 tw-px-6 tw-text-sm tw-border tw-border-gray-500 tw-font-medium tw-rounded focus:tw-outline-none hover:tw-shadow-md tw-inline-flex tw-items-center tw-transition-colors active:tw-bg-gray-200" onClick={() => handleFileInputClick(i)}>
                                    <span className=""> </span>
                                    <PaperClipSVG className="tw-w-4 tw-h-4 tw-ml-0.5" />
                                </button>
                            }
                        </div>
                    </div>

                    <div className="tw-flex tw-items-center">
                        <ButtonTooltip tooltip="" beforeSVG={<MinusCircleSVG className="tw-w-8 tw-h-8 tw-transition-colors tw-duration-300" />} onClick={() => handleRemove(i)} classButton="tw-text-red-500 active:tw-text-red-600" />
                    </div>
                </div>
            )}

            <div className="tw-flex tw-justify-end tw-items-center tw-py-1">
                <div className="tw-text-xs tw-italic tw-text-gray-600 tw-mr-2">
                      
                </div>

                <ButtonTooltip tooltip=" " beforeSVG={<PlusCircleSVG className="tw-w-8 tw-h-8 tw-transition-colors tw-duration-300" />} onClick={handleAdd} classButton={`tw-text-green-500 active:tw-text-green-600 tw-mr-2 ${validate && form.length < 1 && 'tw-border tw-border-red-500 tw-border-dashed'}`} />
            </div>

            <div className="tw-flex tw-justify-end">
                <ButtonTooltip classAppend="tw-mt-6 tw-mb-4 tw-mr-4" classButton="tw-px-8 tw-py-2 tw-bg-blue-800 active:tw-bg-blue-700 tw-text-15px" classLabel="tw-text-white" label="" onClick={handleSubmit} />
            </div>
        </div>
    )
}

export default UrgudulClusters
