import React, { useContext, useEffect, useState } from 'react'
import FormInline from 'components/urgudul_components/formInline'
import HelpPopup from 'components/help_popup/helpPopup'
import PenSVG from 'assets/svgComponents/penSVG'
import FormRichText from 'components/urgudul_components/formRichText'
import ButtonTooltip from 'components/button_tooltip/buttonTooltip'
import axios from 'axiosbase'
import UrgudulContext from 'components/utilities/urgudulContext'
import AlertContext from 'components/utilities/alertContext'
import { useHistory } from 'react-router-dom'
import getLoggedUserToken from 'components/utilities/getLoggedUserToken'
import CalendarSVG from 'assets/svgComponents/calendarSVG'


const initialState = {
    project_duration: null,
    project_start: null,
    project_end: null,
    project_introduction: null,
    preperation: null,
    identified_problems: null,
    suggested_solutions: null,
    expected_result: null,
}

const quillTypes = [
    'project_introduction',
    'preperation',
    'identified_problems',
    'suggested_solutions',
    'expected_result',
]

function UrgudulBreakdown() {
    const [form, setForm] = useState(initialState)

    const UrgudulCtx = useContext(UrgudulContext)

    useEffect(() => {
        if (UrgudulCtx.data.id !== undefined) {
            let temp = {}
            Object.keys(initialState).forEach(key => {
                if (UrgudulCtx.data[key] && UrgudulCtx.data[key] !== null) temp[key] = UrgudulCtx.data[key]
            })
            setForm(prev => ({ ...prev, ...temp }))
        }
    }, [UrgudulCtx.data.id])

    const handleInput = (e) => {
        setForm({ ...form, [e.target.name]: e.target.value })
    }

    const handleInputFormat = (values, name) => {
        setForm({ ...form, [name]: values.value })
    }

    const handleSetForm = (key, value) => {
        setForm(prev => ({ ...prev, [key]: value }))
    }

    useEffect(() => {
        if (form.project_duration && form.project_start) {
            const startDate = new Date(form.project_start)
            const endDate = new Date(startDate.setMonth(startDate.getMonth() + +form.project_duration))
            setForm(prev => ({ ...prev, project_end: endDate.toISOString().split('T')[0] }))
        }
    }, [form.project_duration, form.project_start])

    const AlertCtx = useContext(AlertContext)

    const history = useHistory()

    const handleSubmit = () => {
        setValidate(true)
        const allValid = Object.keys(form).every(key => !checkInvalid(form[key], quillTypes.includes(key) && 'quill'))

        if (UrgudulCtx.data.id) {
            if (allValid) {
                axios.put(`projects/${UrgudulCtx.data.id}`, form, {
                    headers: {
                        'Authorization': getLoggedUserToken()
                    }
                }).then(res => {
                    UrgudulCtx.setData({ ...UrgudulCtx.data, ...res.data.data })
                    AlertCtx.setAlert({ open: true, variant: 'success', msg: '  .' })
                    history.push('/urgudul/6')
                }).catch(err => {
                    AlertCtx.setAlert({ open: true, variant: 'error', msg: ' ,  .' })
                })
            } else {
                AlertCtx.setAlert({ open: true, variant: 'normal', msg: '    .    .' })
            }
        } else {
            AlertCtx.setAlert({ open: true, variant: 'normal', msg: '   .     ,  .' })
            history.push('/urgudul/1')
        }
    }

    const [validate, setValidate] = useState(false)

    const checkInvalid = (value, type) => {
        switch (value) {
            case null:
                return true
            case '':
                return true
            case '<p><br></p>':
                if (type === 'quill') return true
                else return false
            default:
                return false
        }
    }

    return (
        <div className="tw-mt-8 tw-py-2 tw-rounded-lg tw-shadow-md tw-min-w-min tw-w-11/12 tw-max-w-5xl tw-mx-auto tw-border-t tw-border-gray-100 tw-bg-white tw-divide-y tw-divide-dashed">
            <div className="">
                <div className="tw-font-medium tw-p-3 tw-flex tw-items-center tw-text-15px">
                    <span className="tw-text-blue-500 tw-text-xl tw-mx-2">B</span>
                    <span className="tw-leading-tight">-  </span>

                    <HelpPopup classAppend="tw-ml-auto tw-mr-2 sm:tw-ml-12" main="   ,   ,   ." position="bottom" />
                </div>

                {UrgudulCtx.data.project_number &&
                    <div className="tw-ml-5 tw-mb-2 tw-font-medium tw-text-13px">
                         :
                        <span className="tw-text-blue-500 tw-ml-2">{UrgudulCtx.data.project_number}</span>
                    </div>
                }
            </div>

            <div className="tw-flex-grow tw-grid tw-grid-cols-1 md:tw-grid-cols-2 tw-place-items-start">
                <div className="tw-w-full tw-max-w-lg tw-flex">
                    <FormInline label="  " type="numberFormat" formats={{ format: '# ' }} value={form.project_duration || ''} name="project_duration" onChange={handleInputFormat} classAppend="tw-flex-grow" classInput="tw-w-20" invalid={validate && checkInvalid(form.project_duration)} />

                    <div className="tw-relative tw-w-2">
                        <HelpPopup classAppend="tw-right-5 tw-top-1" main="     . ()" position="top-left" />
                    </div>
                </div>

                <FormInline label="  " type="date" value={form.project_start || ''} name="project_start" onChange={handleInput} classAppend="tw-w-full tw-max-w-lg" classInput="tw-w-40" invalid={validate && checkInvalid(form.project_start)} />

                <div className="tw-text-sm tw-p-3 tw-flex tw-flex-col tw-items-start tw-ml-6">
                    <div className="tw-font-medium">
                          :
                    </div>
                    <div className="tw-mt-1 tw-bg-indigo-50 tw-rounded tw-py-1 tw-pl-2 tw-pr-4 tw-text-sm tw-text-indigo-500 tw-font-medium tw-flex tw-items-center">
                        <CalendarSVG className="tw-w-5 tw-h-5 tw-flex-shrink-0 tw-text-gray-600 tw-mr-2" />
                        {form.project_end || 'yyyy-mm-dd'}
                    </div>
                </div>
            </div>

            <div className="tw-w-full">
                <div className="tw-flex tw-items-center tw-p-2 tw-mt-1">
                    <PenSVG className={`tw-w-5 tw-h-5 ${validate && checkInvalid(form.project_introduction, 'quill') ? 'tw-text-red-500' : 'tw-text-gray-600'} tw-transition-colors`} />
                    <span className={`tw-ml-2 tw-text-sm tw-font-medium ${validate && checkInvalid(form.project_introduction, 'quill') && 'tw-text-red-500'} tw-transition-colors`}>
                         
                    </span>

                    <HelpPopup classAppend="tw-ml-auto" main="  ,  ,     ." list={['          ?', '     ?     .']} position="top-left" />
                </div>

                <div className="tw-py-2 tw-px-4 tw-h-40 tw-resize-y tw-overflow-y-hidden" style={{ minHeight: '128px', maxHeight: '768px' }}>
                    <FormRichText modules="small" value={form.project_introduction || ''} name="project_introduction" setForm={handleSetForm} />
                </div>
            </div>

            <div className="tw-w-full">
                <div className="tw-flex tw-items-center tw-p-2 tw-mt-1">
                    <PenSVG className={`tw-w-5 tw-h-5 ${validate && checkInvalid(form.preperation, 'quill') ? 'tw-text-red-500' : 'tw-text-gray-600'} tw-transition-colors`} />
                    <span className={`tw-ml-2 tw-text-sm tw-font-medium ${validate && checkInvalid(form.preperation, 'quill') && 'tw-text-red-500'}`}>
                         
                    </span>

                    <HelpPopup classAppend="tw-ml-auto" main="     .    ,    .         .                  ." position="top-left" />
                </div>

                <div className="tw-py-2 tw-px-4 tw-h-40 tw-resize-y tw-overflow-y-hidden" style={{ minHeight: '128px', maxHeight: '768px' }}>
                    <FormRichText modules="small" value={form.preperation || ''} name="preperation" setForm={handleSetForm} />
                </div>
            </div>

            <div className="tw-w-full">
                <div className="tw-flex tw-items-center tw-p-2 tw-mt-1">
                    <PenSVG className={`tw-w-5 tw-h-5 ${validate && checkInvalid(form.identified_problems, 'quill') ? 'tw-text-red-500' : 'tw-text-gray-600'} tw-transition-colors`} />
                    <span className={`tw-ml-2 tw-text-sm tw-font-medium ${validate && checkInvalid(form.identified_problems, 'quill') && 'tw-text-red-500'} tw-transition-colors`}>
                         
                    </span>

                    <HelpPopup classAppend="tw-ml-auto" main="    ,          ?" position="top-left" />
                </div>

                <div className="tw-py-2 tw-px-4 tw-h-40 tw-resize-y tw-overflow-y-hidden" style={{ minHeight: '128px', maxHeight: '768px' }}>
                    <FormRichText modules="small" value={form.identified_problems || ''} name="identified_problems" setForm={handleSetForm} />
                </div>
            </div>

            <div className="tw-w-full">
                <div className="tw-flex tw-items-center tw-p-2 tw-mt-1">
                    <PenSVG className={`tw-w-5 tw-h-5 ${validate && checkInvalid(form.suggested_solutions, 'quill') ? 'tw-text-red-500' : 'tw-text-gray-600'} tw-transition-colors`} />
                    <span className={`tw-ml-2 tw-text-sm tw-font-medium ${validate && checkInvalid(form.suggested_solutions, 'quill') && 'tw-text-red-500'} tw-transition-colors`}>
                           
                    </span>

                    <HelpPopup classAppend="tw-ml-auto" main="          ?            ." position="top-left" />
                </div>

                <div className="tw-py-2 tw-px-4 tw-h-40 tw-resize-y tw-overflow-y-hidden" style={{ minHeight: '128px', maxHeight: '768px' }}>
                    <FormRichText modules="small" value={form.suggested_solutions || ''} name="suggested_solutions" setForm={handleSetForm} />
                </div>
            </div>

            <div className="tw-w-full">
                <div className="tw-flex tw-items-center tw-p-2 tw-mt-1">
                    <PenSVG className={`tw-w-5 tw-h-5 ${validate && checkInvalid(form.expected_result, 'quill') ? 'tw-text-red-500' : 'tw-text-gray-600'} tw-transition-colors`} />
                    <span className={`tw-ml-2 tw-text-sm tw-font-medium ${validate && checkInvalid(form.expected_result, 'quill') && 'tw-text-red-500'}`}>
                                
                    </span>

                    <HelpPopup classAppend="tw-ml-auto" main="                 " list={['    .', '           .', '(         .)']} position="top-left" />
                </div>

                <div className="tw-py-2 tw-px-4 tw-h-40 tw-resize-y tw-overflow-y-hidden" style={{ minHeight: '128px', maxHeight: '768px' }}>
                    <FormRichText modules="small" value={form.expected_result || ''} name="expected_result" setForm={handleSetForm} />
                </div>
            </div>

            <div className="tw-flex tw-justify-end">
                <ButtonTooltip classAppend="tw-mt-6 tw-mb-4 tw-mr-4" classButton="tw-px-8 tw-py-2 tw-bg-blue-800 active:tw-bg-blue-700 tw-text-15px" classLabel="tw-text-white" label="" onClick={handleSubmit} />
            </div>
        </div>
    )
}

export default UrgudulBreakdown
